<template>
    <div id="app">
        <Keypress key-event="keyup" :key-code="32" @success='showdialog' />
        <div id="bcover" v-show="dialog">
            <div id="test2">
                <div class="explain1">GUIDE: Try to move your Mouse <br>
                to navigate in this tunnel.</div>
                <div class="explain2"><br><br>- press 'space' to start your journey -</div>
            </div>
        </div> 
        <div id="creator04">
            <a href="" target="_blank" rel="noopener">  # created by NatalieJ </a>
        </div>    
        <div id="container"></div>
            <script id="vertexShader" type="x-shader/x-vertex">
                void main() {
                    gl_Position = vec4( position, 1.0 );
                }
            </script>
            <script id="fragmentShader" type="x-shader/x-fragment">
                uniform float iTime;
                uniform vec2 iResolution;
                uniform vec2 iMouse; 
                uniform sampler2D iChannel0;
                uniform float height;
                const float pi = 3.14159265;

                vec2 R(vec2 p, float a) {
                    p = cos(a)*p+sin(a)*vec2(p.y, -p.x);
                    return p;
                }
                float pn( in vec3 p )
                {
                    vec3 ip = floor(p);
                    p = fract(p);
                    p *= p*(3.0-2.0*p);
                    vec2 uv = (ip.xy+vec2(37.0,17.0)*ip.z) + p.xy;
                    uv = textureLod( iChannel0, (uv+ 0.5)/256.0, 0.0 ).yx;
                    return mix( uv.x, uv.y, p.z );
                }

                float fpn(vec3 p) {
                    return pn(p*.06125)*.57   + pn(p*.125)*.28 + pn(p*.25)*.15;
                }

                float rand(vec2 co){
                    return fract(sin(dot(co*0.123,vec2(12.9898,78.233))) * 43758.5453);
                }

                float cosNoise( in vec2 p ){
                    return 0.5*( sin(p.x) + sin(p.y) );
                }

                const mat2 m2 = mat2(1.6,-1.2, 1.2, 1.6);

                float sdTorus( vec3 p, vec2 t )
                {
                return length( vec2(length(p.xz)-t.x*1.2,p.y) )-t.y;
                }

                float smin( float a, float b, float k )
                {
                    float h = clamp( 0.5 + 0.5*(b-a)/k, 0.0, 1.0 );
                    return mix( b, a, h ) - k*h*(1.0-h);
                }

                float SunSurface( in vec3 pos )
                {
                    float h = 0.0;
                    vec2 q = pos.xz*0.5;
                    float s = 0.7;
                    float d2 = 0.0;
                    for( int i=0; i<7; i++ )
                    {
                        h += s*cosNoise( q ); 
                        q = m2*q*0.85; 
                        q += vec2(2.41,8.13);
                        s *= 0.48 + 0.2*h;
                    }
                    h *= 2.0;                   
                    float d1 = pos.y - h;
                    vec3 r1 = mod(0.3+pos+1.0,0.2)-5.0;
                    r1.y = pos.y-0.1 - 0.7*h + 0.5*sin( 3.0*iTime+pos.x + 3.0*pos.z);
                    float c = cos(pos.y); 
                    float s1 = 1.0;
                    r1.xz=c*r1.xz+s1*vec2(r1.z, -r1.x);
                    d2 = sdTorus( r1.xzy, vec2(clamp(abs(pos.x/pos.z),0.7,2.5), 0.20) );
                    return smin( d1, d2, 1.0 );
                }

                float map(vec3 p) {
                    p.z += 1.;
                    p.yz = R(p.yz, -0.01);
                    p.xz = R(p.xz, iMouse.x*0.05*pi+iTime*0.07);
                    return (SunSurface(p * 1.777) +  (fpn(p*2.777+iTime*1.777) * 4.777) -  (fpn(p*100.+iTime*-70.) * 0.5) -1.0) * 0.777;
                }

                vec3 firePalette(float i){
                    float T = 1400. + 1300.*i;
                    vec3 L = vec3(9.4, 4.6, 7.4);
                    L = pow(L,vec3(5.0)) * (exp(1.43876719683e5/(T*L))-1.0);
                    return 1.0-exp(-10e8/L);
                }

                void main()
                {  
                    vec3 rd = normalize(vec3((gl_FragCoord.xy-height*iResolution.xy)/iResolution.x, 1.));
                    float move_v =-20.;
                    float move_y = 0.;
                    vec3 ro = vec3(0., move_y, move_v);
                    float ld=0., td=0., w=0.;
                    float d=1., t=1.;                    
                    const float h = .1;
                    
                    vec3 tc = vec3(0.);
                    vec2 pos = ( gl_FragCoord.xy / iResolution.xy );
                    vec2 seed = pos + fract(iTime);

                    t=(1.+0.2*rand(seed));
                        
                    for (int i=0; i<56; i++) {

                        if(td>(1.-1./80.) || d<0.001*t || t>40.)break;

                        d = map(ro+t*rd); 
                        d=max(d,-8.);

                        float dy2 = 0.2;
                        float h = dy2;
                        ld = (h - d) * step(d, h);
                        float pretty = 5.;
                        w = (pretty - td) * ld;     
                        float redlight = 90.;
                        tc += w*w + 1./redlight;
                        td += w + 1./250.;

                        float delicate =0.2;
                        d = max(d, 0.03);
                        d=abs(d)*(1.+0.2*rand(seed*vec2(i)));                       

                        t += d*0.5;
                        float dynamics =0.5;
                        t += d*0.5;
                        
                    }
                    tc = firePalette(tc.x);
                    float light2 = 1.82;
                    float light3 = 1.;
                    tc *= 1. / exp( ld * light2 ) * 1.05;
                        
                    gl_FragColor = vec4(sqrt(tc), 1.0);
                    gl_FragColor = vec4(vec3((gl_FragColor.r + gl_FragColor.g + gl_FragColor.b) / 3.0), 1.0);
                    gl_FragColor *= gl_FragColor * gl_FragColor;
                    gl_FragColor *= vec4(0.45,0.62,0.87,1.0);
                }

            </script>
    </div>
</template>

<script>
import * as THREE from 'three'
export default {
    components: {
        Keypress: () => import('vue-keypress')
    },
    data(){
        return {
            dialog: true
        }
    },
    mounted(){
        this.runWebGL();
    },
    methods: {
        showdialog(response) {
            this.dialog = false
        },
        goTo(name){
            this.$router.push({ name });
        },
        runWebGL(){
                var camera, scene, renderer, uniforms, textureLoader;
                var startTime = Date.now();
                var lastRender = Date.now();
                var minFrameMilliseconds = 33;
                var speed=0.0;
                //var totalspeed=0.0;

                init();
                animate();

                function init() {
                    camera = new THREE.Camera(0, 0, 1);
                    scene = new THREE.Scene();
                    renderer = new THREE.WebGLRenderer();
                    textureLoader = new THREE.TextureLoader();
                    textureLoader.crossOrigin = 'anonymous';
                    uniforms = {
                        iTime: { type: 'f', value: 0.0 },
                        iResolution: { type: 'v2', value: new THREE.Vector2() },
                        iMouse: { type: 'v2', value: new THREE.Vector2() },
                        iChannel0: { type: 't', value: loadTex16() },
                        height: { type: 'f', value: 0.5 },
                    }
                    scene.add(new THREE.Mesh(
                        new THREE.PlaneBufferGeometry(16, 9),
                        new THREE.ShaderMaterial({
                        uniforms: uniforms,
                        vertexShader: document.getElementById('vertexShader').textContent,
                        fragmentShader: document.getElementById('fragmentShader').textContent
                        })
                    ));
                    
                    document.getElementById('container').appendChild(renderer.domElement);
                    window.addEventListener('resize', onResize, false);
                    window.addEventListener('mousemove', onMouseMove, false);
                    //window.addEventListener( 'wheel', onMouseWheel, false );
                    document.addEventListener('keydown', keyDown);
                    document.addEventListener('keyup', keyUp);
                    onResize();
                    requestAnimationFrame(animate);
                }

                function loadTex16() {
                    var texture = textureLoader.load('https://rcwcheng.github.io//4sKSzw.jpg');
                    texture.wrapS = THREE.RepeatWrapping;
                    texture.wrapT = THREE.RepeatWrapping;
                    texture.flipY = false;
                    return texture;
                }

                function onResize() {
                uniforms.iResolution.value.x = window.innerWidth;
                uniforms.iResolution.value.y = window.innerHeight;
                renderer.setSize(window.innerWidth, window.innerHeight);
                }

                function onMouseMove(event) {
                    uniforms.iMouse.value.x = event.x / uniforms.iResolution.value.x;
                    uniforms.iMouse.value.y = event.y/ uniforms.iResolution.value.y;
                }
                function keyDown(e){
                    if(e.keyCode == 38 || e.keyCode == 40){
                        e.preventDefault();
                    }
                    if(e.keyCode == 38) {
                        //uniforms.iGlobalTime.value += 3.;
                        speed =  -0.006;
                    }
                    if(e.keyCode == 40) {
                        //uniforms.iGlobalTime.value -= 3.;
                        speed = 0.006;
                    }
                }
                
                function keyUp(e){
                    if(e.keyCode == 38) {
                        speed = 0.;
                        //totalspeed = 0.;
                    }
                    if(e.keyCode == 40) {
                        speed = 0.;
                        //totalspeed = 0.;
                    }
                }

                function animate() {
                    requestAnimationFrame(animate);
                    //uniforms.height.value += speed;
                    uniforms.height.value = (uniforms.iMouse.value.y * -1.) + 1.;
                    if(Date.now() > lastRender + minFrameMilliseconds) {
                        lastRender = Date.now();
                        uniforms.iTime.value = 0.001 * (lastRender - startTime);
                        renderer.render(scene, camera);
                    }
                }
            }
        }

    }
</script>

