<template>
    <div id="app">
        <Keypress key-event="keyup" :key-code="32" @success='showdialog' />
        <div id="bcover" v-show="dialog">
            <div id="test2">
                <div class="explain1">GUIDE: Try to move your Mouse + press 'up' and 'down'<br>
                to navigate in this tunnel.</div>
                <div class="explain2"><br><br>- press 'space' to start your journey -</div>
            </div>
        </div> 
        <div id="test">
            <a href="" target="_blank" rel="noopener">  # "Flying through the mountains" created by Natalie.J  </a>
        </div> 
        <div id="container"></div>
            <div id="but">
                <button @click="runWebGL"> Explore! </button></div>
            <script id="vertexShader" type="x-shader/x-vertex">
                void main() {
                    gl_Position = vec4( position, 1.0 );
                }
            </script>
            <script id="fragmentShader" type="x-shader/x-fragment">
                uniform float iTime;
                uniform vec3 iResolution;
                uniform vec2 iMouse;
                uniform float margin;

                vec2 hash(vec2 p)
                {
                    vec3 p3 = fract(vec3(p.xyx) * vec3(.1031, .1030, .0973));
                    p3 += dot(p3.zxy, p3.yxz+19.19);
                    return -1.0 + 2.0*fract(vec2(p3.x * p3.y, p3.z*p3.x));
                }

                float noise(in vec2 p)
                {
                    p *= 0.45;
                    const float K1 = 0.366025404;
                    const float K2 = 0.211324865;
                    vec2 i = floor( p + (p.x+p.y)*K1 );

                    vec2 a = p - i + (i.x+i.y)*K2;
                    vec2 o = (a.x>a.y) ? vec2(1.0,0.0) : vec2(0.0,1.0);
                    vec2 b = a - o + K2;
                    vec2 c = a - 1.0 + 2.0*K2;

                    vec3 h = max( 0.5-vec3(dot(a,a), dot(b,b), dot(c,c) ), 0.0 );

                    vec3 n = h*h*h*h*vec3( dot(a,hash(i+0.0)), dot(b,hash(i+o)), dot(c,hash(i+1.0)));

                    return dot( n, vec3(38.0) );
                    
                }

                mat2 rot(in float a){
                    float c = cos(a), s = sin(a);
                    return mat2(c,s,-s,c);
                }

                float fbm(in vec2 p, in vec2 of)
                {	
                    float time3 = (iTime*1. + ((1.0+sin(iTime*0.9 + sin(iTime*0.34+ cos(iTime*0.21)))))*1.5);

                    p *= rot(time3*0.05);
                    p += of;
                    float z=2.;
                    float rz = 0.;
                    vec2 bp = p;
                    for (float i= 1.;i <9.;i++ )
                    {
                        rz+= noise(p*rot(float(i)*2.3)+ iTime*0.5)/z;
                        z *= 3.2;
                        p *= 2.0;
                    }
                    return rz;
                }

                vec2 grdf(in vec2 p, in vec2 of)
                {
                    vec2 ep = vec2(0.0,0.0005);
                    vec2 d = vec2(fbm(p - ep.yx, of) - fbm(p + ep.yx, of),fbm(p - ep.xy, of) - fbm(p + ep.xy, of));
                    d /= length(d);
                    return d;
                }

                void main()
                {
                    float time2 = (margin*2.1 + ((1.0+sin(margin + sin(margin*0.4+ cos(margin*0.1)))))*1.5);
                    float time4 = (iTime*0.5 + ((1.0+sin(iTime*0.8 + sin(iTime*0.14+ cos(iTime*0.15)))))*1.2);

                    vec2 p = gl_FragCoord.xy / iResolution.xy;
                    p.x -= iMouse.x * 0.1;
                    p.y -= iMouse.y * 0.1;
                    p.x *= iResolution.x/iResolution.y;
                    p *= 10.5;
                    
                    float t1 = mod(time2*0.35,4.);
                    float t2 = mod(time2*0.35 + 1.,4.);
                    
                    vec2 p1 = p*(4.0-t1);
                    vec2 p2 = p*(4.0-t2);
                    
                    vec2 fld = grdf(p1, vec2(time4*0.2,iTime*0.0));
                    
                    vec2 fld2 = grdf(p2, vec2(time4*0.2,iTime*0.0) + 2.2);

                    gl_FragColor = vec4(fld.y*0.6+fld2.y*0.4, fld.y*0.99+fld2.y*0.2, fld.y*0.6, 1.0);
                }
            </script>
    </div>
</template>

<script>
import * as THREE from 'three'
export default {
    components: {
        Keypress: () => import('vue-keypress')
    },
    data(){
        return {
            dialog: true
        }
    },
    mounted(){
        this.runWebGL();
    },
    methods: {
        showdialog(response) {
            this.dialog = false
        },
        goTo(name){
            this.$router.push({ name });
        },
        runWebGL(){
                var container;
                var camera, scene, renderer;
                var uniforms;
                var speed=0.0;
                var startTime;

                init();
                animate();
                function init() {
                container = document.getElementById('container');
                startTime = Date.now();
                camera = new THREE.Camera();
                camera.position.z = 1;
                scene = new THREE.Scene();
                var geometry = new THREE.PlaneBufferGeometry(16, 9);
                uniforms = {
                    iTime: { type: "f", value: 1.0 },
                    iResolution: { type: "v2", value: new THREE.Vector2()},
                    iMouse: { type: 'v2', value: new THREE.Vector2()},
                    margin: { type: "f", value: 4.35 }
                };
                var material = new THREE.ShaderMaterial( {
                    uniforms: uniforms,
                    vertexShader: document.getElementById('vertexShader').textContent,
                    fragmentShader: document.getElementById('fragmentShader').textContent
                } );
                var mesh = new THREE.Mesh(geometry, material);
                scene.add(mesh);
                renderer = new THREE.WebGLRenderer();
                container.appendChild(renderer.domElement);
                onWindowResize();
                window.addEventListener('resize', onWindowResize, false);
                window.addEventListener('mousemove', onMouseMove, false);
                //window.addEventListener( 'wheel', onMouseWheel, false );
                document.addEventListener('keydown', keyDown);
                document.addEventListener('keyup', keyUp);
            }
                function onWindowResize() {
                    uniforms.iResolution.value.x = window.innerWidth;
                    uniforms.iResolution.value.y = window.innerHeight;
                    renderer.setSize(window.innerWidth, window.innerHeight);
                }
                
                function map(value, min1, max1, min2, max2) {
                    return (min2 + (value - min1) * (max2 - min2) / (max1 - min1));
                }

                function onMouseMove(event) {
                    var mx, my;
                    mx = event.x / uniforms.iResolution.value.x;
                    my = event.y/ uniforms.iResolution.value.y;
                    uniforms.iMouse.value.x = map(mx, 0., 1., -1., 1.);
                    uniforms.iMouse.value.y = map(my, 0., 1., -1., 1.);
                }

                
                function keyDown(e){
                    if(e.keyCode == 38 || e.keyCode == 40){
                        e.preventDefault();
                    }
                    if(e.keyCode == 38) {
                        //uniforms.iGlobalTime.value += 3.;
                        speed = 0.015;
                    }
                    if(e.keyCode == 40) {
                        //uniforms.iGlobalTime.value -= 3.;
                        speed = -0.015;
                    }
                }
                
                function keyUp(e){
                    if(e.keyCode == 38) {
                        speed = 0.;
                        //totalspeed = 0.;
                    }
                    if(e.keyCode == 40) {
                        speed = 0.;
                        //totalspeed = 0.;
                    }
                }

                function animate() {
                    requestAnimationFrame(animate);
                    uniforms.margin.value += speed;
                    render();
                }

                function render() {
                    var currentTime = Date.now();
                    uniforms.iTime.value = (currentTime - startTime) * 0.001;
                    renderer.render(scene, camera);
                }
            }
        }

    }

</script>

