<template>
    <div id="app">
        <Keypress key-event="keyup" :key-code="32" @success='showdialog' />
        <div id="bcover" v-show="dialog">
            <div id="test2">
                <div class="explain1">GUIDE: Try to move your Mouse <br>
                to navigate in this tunnel.</div>
                <div class="explain2"><br><br>- press 'space' to start your journey -</div>
            </div>
        </div> 
        <div id="creator03">
            <a href="" target="_blank" rel="noopener">  # created by NatalieJ  </a>
        </div> 
        <div id="container"></div>
            <div id="but">
                <button @click="runWebGL"> Explore! </button></div>
            <script id="vertexShader" type="x-shader/x-vertex">
                void main() {
                    gl_Position = vec4( position, 1.0 );
                }
            </script>
            <script id="fragmentShader" type="x-shader/x-fragment">
                uniform float iTime;
                uniform vec3 iResolution;
                
                vec3 R(vec3 p, vec3 a, float r){
                    return mix(a*dot(p,a),p,cos(r))+sin(r)*cross(p,a);}

                uniform vec2 iMouse; 
                uniform float fract;

                void main()
                {
                    vec2 C = gl_FragCoord.xy;
                    gl_FragColor=vec4(0);
                    vec3 p,q,r=iResolution, d=normalize(vec3((C-0.5*r.xy)/r.y,1));
                    
                    float color = 0.3;
                    float bright = -0.2;
                    float bright2 = 0.015;
                    
                    for(float i=0.,s,e,g=0.;++i<80.;gl_FragColor.xyz+=bright2*abs(cos(d+log(s)*color))*exp(bright*i*i*e))
                    {
                        p=g*d;
                        p-=vec3(iMouse.x,.1,iMouse.y);
                        p=R(p,normalize(vec3(1,1,0.)),iTime*.2);
                        q=p;
                        s=0.6;
                        for(int j=0;j++<15;s*=e)
                            p=sign(p)*(1.2-abs(p-1.2)),
                            p=p*(e=8./clamp(dot(p,p),.3,5.5))+q*fract;
                        g+=e=length(p)/s;
                    }
                    gl_FragColor.x *= 1.1;
                    gl_FragColor.y *= 0.95;
                    gl_FragColor.z *= 1.0;
                    gl_FragColor.w = 1.;
                }
            </script>
    </div>
</template>

<script>
import * as THREE from 'three'
export default {
    components: {
        Keypress: () => import('vue-keypress')
    },
    data(){
        return {
            dialog: true
        }
    },
    mounted(){
        this.runWebGL();
    },
    methods: {
        showdialog(response) {
            this.dialog = false
        },
        goTo(name){
            this.$router.push({ name });
        },
        runWebGL(){
                var container;
                var camera, scene, renderer;
                var uniforms;
                var speed=0.0;
                var startTime;
                var flag = 0.01;
                init();
                animate();
                function init() {
                container = document.getElementById('container');
                startTime = Date.now();
                camera = new THREE.Camera();
                camera.position.z = 1;
                scene = new THREE.Scene();
                var geometry = new THREE.PlaneBufferGeometry(16, 9);
                uniforms = {
                    iTime: { type: "f", value: 1.0 },
                    iResolution: { type: "v3", value: new THREE.Vector3()},
                    iMouse: { type: 'v2', value: new THREE.Vector2()},
                    fract: { type: "f", value: 0.5 }
                };
                var material = new THREE.ShaderMaterial( {
                    uniforms: uniforms,
                    vertexShader: document.getElementById('vertexShader').textContent,
                    fragmentShader: document.getElementById('fragmentShader').textContent
                } );
                var mesh = new THREE.Mesh(geometry, material);
                scene.add(mesh);
                renderer = new THREE.WebGLRenderer();
                container.appendChild(renderer.domElement);
                onWindowResize();
                window.addEventListener('resize', onWindowResize, false);
                window.addEventListener('mousemove', onMouseMove, false);
                //window.addEventListener( 'wheel', onMouseWheel, false );
                //document.addEventListener('keydown', keyDown);
                //document.addEventListener('keyup', keyUp);
            }
                function onWindowResize() {
                    uniforms.iResolution.value.x = window.innerWidth;
                    uniforms.iResolution.value.y = window.innerHeight;
                    uniforms.iResolution.value.z = 1.;
                    renderer.setSize(window.innerWidth, window.innerHeight);
                }
                
                function map(value, min1, max1, min2, max2) {
                    return (min2 + (value - min1) * (max2 - min2) / (max1 - min1));
                }

                function onMouseMove(event) {
                    var mx, my;
                    mx = event.x / uniforms.iResolution.value.x;
                    my = event.y/ uniforms.iResolution.value.y;
                    uniforms.iMouse.value.x = map(mx, 0., 1., -0.5, 0.5);
                    uniforms.iMouse.value.y = map(my, 0., 1., 30., -1.);
                }

                /*
                function keyDown(e){
                    if(e.keyCode == 38 || e.keyCode == 40){
                        e.preventDefault();
                    }
                    if(e.keyCode == 38) {
                        //uniforms.iGlobalTime.value += 3.;
                        speed = -0.5;
                    }
                    if(e.keyCode == 40) {
                        //uniforms.iGlobalTime.value -= 3.;
                        speed = 0.5;
                    }
                }
                
                function keyUp(e){
                    if(e.keyCode == 38) {
                        speed = 0.;
                        //totalspeed = 0.;
                    }
                    if(e.keyCode == 40) {
                        speed = 0.;
                        //totalspeed = 0.;
                    }
                }*/

                function animate() {
                    requestAnimationFrame(animate);
                    //uniforms.iTime.value += speed;
                    render();
                }

                function render() {
                    var currentTime = Date.now();
                    uniforms.iTime.value = (currentTime - startTime) * 0.001;
                    uniforms.fract.value += flag;
                    if(uniforms.fract.value >= 1.2){
                        flag = -0.01;}
                    else if(uniforms.fract.value <= 0){
                        flag = 0.01;}
                    renderer.render(scene, camera);
                }
            }
        }

    }

</script>

