<template lang="pug">
  #app
    keypress(key-event='keyup', :key-code='76', @success='someMethod')
    router-view(:key="$route.path") 
    transition(name="page" mode="out-in")
</template>

<script>
export default {
  components: {
    Keypress: () => import('vue-keypress')
  },
  name: "app",
  methods: {
    someMethod(response) {
      this.goTo("MainPage")
    },
    goTo(name){
      this.$router.push({ name });
    }
  }
}
</script>

