<template>
    <div id="app">
        <Keypress key-event="keyup" :key-code="32" @success='showdialog' />
        <div id="bcover" v-show="dialog">
            <div id="test2">
                <div class="explain1">GUIDE: Try to press 'up' and 'down' <br>
                to navigate in this tunnel.</div>
                <div class="explain2"><br><br>- press 'space' to start your journey -</div>
            </div>
        </div> 
        <div id="creator02">
            <a href="https://www.shadertoy.com/view/NdfGDl" target="_blank" rel="noopener">  # Original authhor: evvvvil </a>
        </div>    
        <div id="container"></div>
            <script id="vertexShader" type="x-shader/x-vertex">
                void main() {
                    gl_Position = vec4( position, 1.0 );
                }
            </script>
            <script id="fragmentShader" type="x-shader/x-fragment">
                uniform float iGlobalTime;
                uniform vec2 iResolution;
                uniform vec2 iMouse; 
                uniform sampler2D iChannel0;
                
                vec2 z,v,vv,e=vec2(.00035,-.00035);
                float tt,b,bb,g=0.,gg=0.,tnoi,res=1.;
                vec3 bp,pp,rd,fo,lp,po,op,no,al,ld,vcp;

                float bo(vec3 p,vec3 r){
                    p=abs(p)-r;
                    return max(max(p.x,p.y),p.z);
                }
                mat2 r2(float r){return mat2(cos(r),sin(r),-sin(r),cos(r));}
                float smin(float a,float b,float k){
                    float h=max(k-abs(a-b),0.);
                    return min(a,b)-h*h*.25/k;}

                float smax(float a,float b,float k){
                    float h=max(k-abs(-a-b),0.);
                    return max(-a,b)+h*h*.25/k;}
                vec4 texNoise(vec2 uv,sampler2D tex){
                    float f = 0.;
                    f+=texture(tex, uv*.125).r*.5;
                    f+=texture(tex,uv*.25).r*.25;
                    f+=texture(tex,uv*.5).r*.125;
                    f+=texture(tex,uv*1.).r*.125;
                    f=pow(f,1.2);return vec4(f*.45+.05);
                }
                vec2 mp(vec3 p)
                {
                    op=p;
                    p.y=mod(p.y-tt*2.,20.)-10.;  
                    vec2 h,t=vec2(bo(p,vec3(3,20,3)),6);
                    vec3 cp=p+vec3(0.,0.0,.3);  
                    t.x = max(t.x,-(length(cp.xz)-3.0));
                    h=vec2(bo(p,vec3(3.05,20,3.05)),3);
                    h.x=max(h.x,-(length(cp.xz)-3.0));
                    t.x=abs(abs(t.x)-.5)-.2;
                    h.x=abs(abs(h.x)-.5)-.3;
                    vcp=p;
                    vcp.y=abs(abs(abs(p.y)-10.)-4.)-2.;
                    t.x=max(t.x,min(vcp.y,p.z+1.2)); 
                    h.x=max(h.x,min(vcp.y+.2,p.z+1.4)); 
                    vcp.yz*=r2(0.785);
                    t.x=max(t.x,-(abs(abs(vcp.y+1.)-.4)-.1)); 
                    t.x=max(t.x,vcp.y-1.5);  
                    h.x=max(h.x,-(abs(abs(vcp.y+1.)-.4)-.3)); 
                    h.x=max(h.x,vcp.y-1.3); 
                    bp=op+vec3(0.,bb,.3);  
                    float glo=length(abs(bp)-vec3(0,2.,0.))-.5;
                    t.x=min(t.x,max(length(abs(bp.xz)-.75)-.1,abs(bp.y)-1.));  
                    float glo2=max(abs(length(bp.xz)-1.)-.5,abs(abs(bp.y)-1.5)-.2);
                    glo=min(glo,glo2);
                    g+=0.1/(0.1+glo*glo*40.);
                    t.x=min(t.x,glo);  
                    t=t.x<h.x?t:h;   
                    h=vec2(length(abs(p.xz)-3.)-0.15,7);
                    h.x=min(h.x,max(abs(length(bp.xz)-1.)-.4,abs(abs(bp.y)-1.5)-.5));  
                    t=t.x<h.x?t:h;    
                    vec3 mop=op+vec3(0,-tt*2.,0);
                    tnoi=texNoise(mop.xy*.06,iChannel0).r*1.2;
                    vec3 tp=op-vec3(0,0,12);  
                    b=sin(op.y-tt*2.)+sin(op.x*.5);  
                    h=vec2(abs(length(tp.xz)-22.+tnoi+b)-.2,5); 
                    h.x=max(abs(h.x)-2.,abs(p.y)-100.);    
                    vec3 prp=vec3(atan(tp.x,tp.z)*3.142*5.,op.y-tt*2.,length(tp.xz)-19.5+b);
                    prp.xy=mod(prp.xy,5.)-2.5;
                    h.x=smin(h.x,length(prp.yz)-1.-sin(mop.x)*.3+tnoi,1.2);
                    h.x=smin(h.x,length(abs(prp.yz)-vec2(1.,0))-.1-sin(prp.x*10.0)*.1-tnoi,.5);  
                    float tun=h.x+1.;
                    h.x=smax((abs(prp.y)-.1),h.x,.5);
                    pp=op+vec3(0,0,-13);pp.xz=abs(pp.xz)-15.+b;
                    h.x=smin(h.x,length(pp.xz)-2.0-sin(mop.y*.5),3.); 
                    tun=abs(tun)-.2; 
                    tun=max(tun,abs(p.y)-100.);
                    g+=0.1/(0.1+tun*tun*4.);
                    h.x=min(h.x,tun);h.x*=0.6;
                    t=t.x<h.x?t:h;
                    return t;
                }
                vec2 tr( vec3 ro,vec3 rd)
                {
                    vec2 h,t=vec2(.1);
                    for(int i=0;i<128;i++){
                        h=mp(ro+rd*t.x);
                        if(h.x<.0001||t.x>65.)
                            break;
                        t.x+=h.x;
                        t.y=h.y;
                    }
                    if(t.x>65.) t.y=0.;
                        return t;
                }

                float a(float d){
                    return clamp(mp(po+no*d).x/d,0.,1.);
                }
                float s(float d){
                    return smoothstep(0.,1.,mp(po+ld*d).x/d);
                }

                vec3 lit(float da,float atn){    
                    ld=normalize(lp-po);
                    float dif=da*max(0.,dot(no,ld)),
                    fr=pow(1.+dot(no,ld),4.),
                    sp=pow(max(dot(reflect(-ld,no),-rd),0.),40.),
                    attn=1.-pow(min(1.,length(lp-po)/atn),4.0); 
                    return attn*mix(sp+al*(a(.1)+.2)*(dif+s(.5)),fo,min(fr,.5)); 
                }
                void mainImage( out vec4 fragColor, in vec2 fragCoord )
                {
                    vec2 vv,uv = vec2(fragCoord.x / iResolution.x, fragCoord.y / iResolution.y);
                    vv=uv;uv-=0.5;vv*=1.-vv;uv/=vec2(iResolution.y/iResolution.x,1);
                    tt=mod(iGlobalTime,62.83)+23.6;
                    bb=-40.+smoothstep(0.,1.,smoothstep(0.,1.,sin(tt*.2)*.5+.5))*80.;
                    b=ceil(cos(tt*.2));
                    vec3 ro=mix(vec3(-9.,00,6.+sin(tt*.2)*4.),vec3(sin(tt*.2)*12.,-bb-sin(bb*.05)*10.,5.0),b),
                    cw=normalize(vec3(0.,mix(-bb*.2,-bb,b),1)-ro),
                    cu=normalize(cross(cw,vec3(0,1,0))),cv=normalize(cross(cu,cw)),co;
                    rd=mat3(cu,cv,cw)*normalize(vec3(uv,.55));
                    co=fo=vec3(.1);
                    z=tr(ro,rd);
                    if(z.y>0.){
                        po=ro+rd*z.x;
                        no=normalize(e.xyy*mp(po+e.xyy).x+e.yyx*mp(po+e.yyx).x+e.yxy*mp(po+e.yxy).x+e.xxx*mp(po+e.xxx).x);    
                        al=mix(vec3(.5,.4,.3),vec3(.2,.2,.2),tnoi*5.0);
                        if(z.y<5.) {
                        v=vcp.yz; 
                        for(int i=0;i<4;i++){
                            v=abs(v)-.3;
                            v*=r2(-.785); v*=1.22;
                            float per=abs(sin(v.x))-.1+clamp(sin(v.y*2.),-.1,.1)*.5;
                            res=min(res,ceil(per));
                            g+=.25/(0.1+(per*per)*(20.-18.9*sin(uv.y*1.5+tt*2.)));  
                        }
                        al=vec3(1.-res);
                        }
                        if(z.y>5.) al=vec3(0.4,.5,.7);
                        if(z.y>6.) al=vec3(0.);
                        lp=vec3(0,-bb,0);    
                        co=lit(3.,10.);    
                        lp=ro;
                        co+=.75*lit(2.,15.);      
                        co=mix(fo,co,exp(-.00003*z.x*z.x*z.x));    
                    }  
                    co=mix(co+g*.2*vec3(.1,.2,.7),fo,min(1.,length(ro.y-op.y)*.02));      
                    fragColor = vec4(pow(co*pow(vv.x*vv.y*15.0,0.25),vec3(.65)),1);
                }
                void main(void) {
                    mainImage(gl_FragColor, gl_FragCoord.xy);
                }
            </script>
    </div>
</template>

<script>
import * as THREE from 'three'
export default {
    components: {
        Keypress: () => import('vue-keypress')
    },
    data(){
        return {
            dialog: true
        }
    },
    mounted(){
        this.runWebGL();
    },
    methods: {
        showdialog(response) {
            this.dialog = false
        },
        goTo(name){
            this.$router.push({ name });
        },
        runWebGL(){
                var camera, scene, renderer, uniforms, textureLoader;
                //var startTime = Date.now();
                var lastRender = Date.now();
                var minFrameMilliseconds = 33;
                var speed=0.0;
                //var totalspeed=0.0;

                init();
                animate();

                function init() {
                    camera = new THREE.Camera(0, 0, 1);
                    scene = new THREE.Scene();
                    renderer = new THREE.WebGLRenderer();
                    textureLoader = new THREE.TextureLoader();
                    textureLoader.crossOrigin = 'anonymous';
                    uniforms = {
                        iGlobalTime: { type: 'f', value: 0.0 },
                        iResolution: { type: 'v2', value: new THREE.Vector2() },
                        iMouse: { type: 'v2', value: new THREE.Vector2() },
                        iChannel0: { type: 't', value: loadTex16() },
                    }
                    scene.add(new THREE.Mesh(
                        new THREE.PlaneBufferGeometry(16, 9),
                        new THREE.ShaderMaterial({
                        uniforms: uniforms,
                        vertexShader: document.getElementById('vertexShader').textContent,
                        fragmentShader: document.getElementById('fragmentShader').textContent
                        })
                    ));
                    
                    document.getElementById('container').appendChild(renderer.domElement);
                    window.addEventListener('resize', onResize, false);
                    window.addEventListener('mousemove', onMouseMove, false);
                    //window.addEventListener( 'wheel', onMouseWheel, false );
                    document.addEventListener('keydown', keyDown);
                    document.addEventListener('keyup', keyUp);
                    onResize();
                    requestAnimationFrame(animate);
                }

                function loadTex16() {
                    var texture = textureLoader.load('https://rcwcheng.github.io/tex1.jpeg');
                    texture.wrapS = THREE.RepeatWrapping;
                    texture.wrapT = THREE.RepeatWrapping;
                    texture.flipY = false;
                    return texture;
                }

                function onResize() {
                uniforms.iResolution.value.x = window.innerWidth;
                uniforms.iResolution.value.y = window.innerHeight;
                renderer.setSize(window.innerWidth, window.innerHeight);
                }

                function onMouseMove(event) {
                    uniforms.iMouse.value.x = event.x;
                    uniforms.iMouse.value.y = event.y;
                }
                function keyDown(e){
                    if(e.keyCode == 38 || e.keyCode == 40){
                        e.preventDefault();
                    }
                    if(e.keyCode == 38) {
                        //uniforms.iGlobalTime.value += 3.;
                        speed = -0.05;
                    }
                    if(e.keyCode == 40) {
                        //uniforms.iGlobalTime.value -= 3.;
                        speed = 0.05;
                    }
                }
                
                function keyUp(e){
                    if(e.keyCode == 38) {
                        speed = 0.;
                        //totalspeed = 0.;
                    }
                    if(e.keyCode == 40) {
                        speed = 0.;
                        //totalspeed = 0.;
                    }
                }

                function animate() {
                    requestAnimationFrame(animate);
                    //totalspeed += speed;
                    uniforms.iGlobalTime.value += speed;
                    if(Date.now() > lastRender + minFrameMilliseconds) {
                        lastRender = Date.now();
                        //uniforms.iGlobalTime.value = 0.001 * (lastRender - startTime);
                        renderer.render(scene, camera);
                    }
                }
            }
        }

    }
</script>

